import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentProvince: "新疆",
  },
  mutations: {
    SET_CURRENT_PROVINCE(state, newVal) {
      state.currentProvince = newVal;
    },
  },
  actions: {
    setCurrentProvince({ commit }, newVal) {
      commit("SET_CURRENT_PROVINCE", newVal);
    },
  },
  modules: {},
});
