import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "*", // 未匹配到的路径
    redirect: "/index", // 默认重定向
  },
  {
    path: "/",
    name: "index",
    component: () => import("../views/index2.vue"),
    // component: () => import('../views/index.vue')
  },
  {
    path: "/index",
    name: "index2",
    component: () => import("../views/index2.vue"),
  },
  {
    path: "/other-page",
    name: "index",
    component: () => import("../views/index.vue"),
  },
];
const router = new VueRouter({
  routes,
});

export default router;
